import type { PropsWithChildren } from 'react';
import { useCallback, useMemo, useState } from 'react';
import styled from 'styled-components';
import { TabsContext } from './TabsContext';

export interface TabsProps {
    defaultActiveItem?: number | string;
    disabled?: boolean;
}

const StyledRoot = styled.div`
    display: flex;
    flex-direction: row;
    border: 1px solid ${(props) => props.theme.token.color.palette.gray[10]};
    border-radius: ${(props) => props.theme.token.radius.border.basic};
`;

function Tabs(props: PropsWithChildren<TabsProps>) {
    const { children, defaultActiveItem, disabled } = props;

    const [activeItem, setActiveItem] = useState(defaultActiveItem);

    const handleChange = useCallback((event: React.MouseEvent<HTMLButtonElement>, id: number | string) => {
        setActiveItem(id);
    }, []);

    const tabsContext = useMemo(() => {
        return {
            activeItem,
            onChange: handleChange,
            disabled,
        };
    }, [activeItem, disabled, handleChange]);

    return (
        <TabsContext.Provider value={tabsContext}>
            <StyledRoot className='tab-menu'>{children}</StyledRoot>
        </TabsContext.Provider>
    );
}

export default Tabs;
Tabs.displayName = 'Tabs';
